.mainSliderContainer {
  width: 100vw;
  height: auto;
  position: relative;
  display: flex;
  align-items: center;
  user-select: none;
  -webkit-user-select: none;
  scroll-behavior: smooth;
}

.slider {
  width: 100%;
  height: 100%;
  white-space: nowrap;
  overflow-x: scroll;
  overflow-y: hidden;
  scrollbar-width: none; /* Firefox */
  scroll-snap-type: x mandatory;
  scroll-behavior: smooth;
  overflow-x: scroll;
}

.slider::-webkit-scrollbar {
  display: none; /* Chrome */
}

.sliderIcon {
  color: #f3f3f3;
  position: absolute;
  cursor: pointer;
  transition: all 0.3s ease-in-out;
}

.sliderIcon.left {
  left: 0;
  z-index: 1;
  opacity: 0;
}

.sliderIcon.right {
  right: 0;
}

.card {
  display: inline-block;
  margin-left: 2vw;
  margin-right: 2vw;
  scroll-snap-align: center;
  scroll-snap-stop: always;
}

.paginationWrapper {
  width: 25%;
  display: flex;
  justify-content: center;
  position: absolute;
  bottom: -5%;
  transform: translate(-50%);
  left: 50%;
}

.paginationEmpty {
  background-color: #191919;
  height: 5px;
  width: 100%;
  border-radius: 5px;
}

.paginationFull {
  background-color: #ffffff;
  height: 5px;
  width: 3%;
  border-radius: 5px;
  position: absolute;
  transition: all 0.4s cubic-bezier(0.68, -0.55, 0.27, 1.55);
}

@media (max-width: 800px) {
  .sliderIcon {
    display: none;
  }

  .slider {
    display: grid;
    gap: 20px 20px;
    grid-auto-flow: column;
    grid-auto-columns: auto;
    grid-auto-rows: auto;
    grid-template-columns: auto auto;
    grid-template-rows: auto auto;
    grid-template-areas:
      ". ."
      ". .";
  }

  .card {
    margin: 0 4vw 0 4vw;
  }

  .paginationWrapper {
    display: none;
  }
}
