.workCard {
  width: 27vw;
  height: clamp(200px, 14vw, 240px);
  background: linear-gradient(135deg, #20202015, #20202023);
  backdrop-filter: blur(10px);
  padding: 15px;
  border-radius: 10px;
  position: relative;
  display: grid;
  grid-template-rows: 10% 45% 25% 20%;
  align-items: end;
  transition: all 0.4s cubic-bezier(0.68, -0.55, 0.27, 1.55);
}

@supports (
  (backdrop-filter: blur(10px)) or (-webkit-backdrop-filter: blur(10px))
) {
  .workCard {
    backdrop-filter: blur(10px);
    -webkit-backdrop-filter: blur(10px);
  }
}

@supports not (backdrop-filter: blur(10px)) {
  .workCard {
    background: linear-gradient(135deg, #20202063, #202020ad);
  }
}

.cardBackground {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background: linear-gradient(225deg, #fcc15b38, #f52c2e38, #8942ad38);
  transition: all 0.4s cubic-bezier(0.68, -0.55, 0.27, 1.55);
  z-index: -1;
  border-radius: 10px;
  opacity: 0;
}

.workCard:hover .cardBackground {
  transition: all 0.4s cubic-bezier(0.68, -0.55, 0.27, 1.55);
  opacity: 1;
}

.dateAndLogo {
  display: flex;
  justify-content: space-between;
  align-items: center;
}

.logo {
  width: 23px;
  height: 23px;
  border-radius: 100%;
  background: white;
}

.date {
  margin: 0;
  color: #8a8a8a;
  font-family: "Open Sans", sans-serif;
  font-weight: lighter;
  font-size: 0.8em;
}

.status span {
  border: 1px solid #8a8a8a;
  border-radius: 7px;
  padding: 1px 17px 1px 17px;
  color: #ffffff;
  font-family: "Open Sans", sans-serif;
  font-weight: lighter;
  font-size: 0.7em;
}

.workName {
  font-family: "Open Sans", sans-serif;
  font-weight: bold;
  font-size: 2em;
  color: #ffffff;
}

.cardFooter {
  display: flex;
  justify-content: space-between;
}

.usedTech span {
  color: #8a8a8a;
  font-family: "Open Sans", sans-serif;
  font-weight: lighter;
  font-size: 0.8em;
}

.viewArrow {
  width: 30px;
  height: 30px;
  color: white;
  transform: rotate(180deg);
  cursor: pointer;
  transition: all 0.4s cubic-bezier(0.68, -0.55, 0.27, 1.55);
}

.viewArrow:hover {
  transform: rotate(180deg) translateX(-3px);
}

@media (max-width: 800px) {
  .workCard {
    width: 85vw;
  }

  .status span {
    font-size: 0.8em !important;
  }

  .usedTech span {
    font-size: 1em !important;
  }
}
